import $loadjs from 'loadjs'

if (!process.env.CDN) {
  import('./react')
} else {
  $loadjs([
    'https://cdnjs.cloudflare.com/ajax/libs/react/17.0.1/umd/react.production.min.js',
    'https://cdnjs.cloudflare.com/ajax/libs/react-dom/17.0.1/umd/react-dom.production.min.js',
  ], 'bundle', {
    async: false,
  })
}

window.document.head.innerHTML += `<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/typeface-open-sans@1.1.13/index.min.css">`
$loadjs.ready('bundle', () => import('./react'))
